*{
    margin: 0;
    padding: 0;
}
.GFG {
    width: 1500px;
}
.img{
    width: 400px;
}
.main-container{
    background-color: dimgray;
    padding: 1rem;
}
.wrapper{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(12rem, 16rem));
    gap:2rem;
    justify-content: center;
}
.card{
    overflow: hidden;
    box-shadow:0 2px 20px gray;
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    cursor: pointer;
    transition: transform 200ms ease-in;
     
}
.card__image{
    height: 12rem;
    width: 100%;
    object-fit: cover;
}
.card__title{
    padding: 1rem;
}
.card__descr{
    padding:0 1rem;
}
.card__button{
    padding: 1rem;
    font-family: inherit;
    font-weight: bold;
    font-size: 1rem;
    margin: 1rem;
    border: 2px solid red;
    background:transparent ;
    color: red;
    border-radius: 30px;
    transition:background 200ms ease-in,color 200ms ease-in;

}
.card:hover{
    transform: scale(1.02);
}
.card__button:hover{
    background-color: red;
    color: white;
}


